import { Outlet } from "react-router-dom";
import General from '../general';
import React, { Component } from 'react';
// function click(){
//   sessionStorage.setItem("accessToken", "");
//   sessionStorage.setItem("isLogin", "false");
//   window.location.href = "/";
// }

export class Layout extends Component {
  // let isLogin = (sessionStorage.getItem("isLogin") === "true");

  constructor(props) {
    super(props);
    const url = window.location.href;
    let favorites = "";
    let home = "";
    if(url.endsWith("favorites")) favorites = "active";
    else home = "active";
    this.state = { status: false, favorites: favorites, home: home };
    this.click = this.click.bind(this);
  }

  componentDidMount() {
    this.loadData();
  }

  click(location){
    if(location === "favorites") this.setState({favorites: "active", home: ""});
    else this.setState({favorites: "", home: "active"});
  }

  render() {
    return (
      <>
        <div className="vh-100 text-white">
          <div className="bg-gradient">
            <nav className="navbar navbar-expand-lg  navbar-dark w-75 m-auto">
              <div className="navbar-brand">Spread hunter</div>
              <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                  <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse w-100" id="navbarNav">
                  <ul className="navbar-nav">
                    <li className="nav-item">
                      <a className={"nav-link " + this.state.home} href="/">Tokens</a>
                    </li>
                    <li className="nav-item">
                      <a className={"nav-link " + this.state.favorites} href="/#/favorites" onClick={()=>{this.click("favorites")}}>Favorites</a>
                    </li>
                    {/* {isLogin?
                    (<>
                      <li className="nav-item">
                        <a className="nav-link" href="/#/symbols">Symbols</a>
                      </li>
                      <div className="d-flex"><button className="btn btn-link text-decoration-none text-danger" onClick={click}>Exit</button></div>
                    </>)
                    :
                    (<>
                      <li className="nav-item">
                        <a className="nav-link" href="/#/login">Login</a>
                      </li>
                    </>)} */}

                  </ul>
                  <div className="d-flex justify-content-end w-100">
                    <div className="mx-1">Server status:</div>
                    {this.state.status ? 
                    <div className="nav-item text-success">Online</div> 
                    :
                    <div className="nav-item text-danger">Offline</div>}
                  </div>
                </div>
            </nav>
          </div>
          <Outlet />
        </div>
      </>
    )
  }

  async loadData() {
    const response = await fetch(`https://${General.getApi()}/SpreadHunter/Status`);
    if (response.ok) {
      var result = await response.json();
      this.setState({ status: result });
    }
  }
};
