import React, { Component } from 'react';
import { Compare } from "../components/Compare";
import { Table, Thead, Tbody, Tr, Th } from 'react-super-responsive-table';
import 'react-super-responsive-table/dist/SuperResponsiveTableStyle.css';
import Pako from 'pako';
import General from '../general';

export class Groups extends Component {

  constructor(props) {
    super(props);
    this.state = { array: [] };
  }

  componentDidMount() {
    this.loadData();
  }

  render() {
    return (
      <div className='container mt-3'>
        <Table className='table table-dark table-hover text-center align-middle mt-3'>
          <Thead>
            <Tr>
              <Th className="bg-gradient" style={{ width: "20%" }}>Name</Th>
              <Th className="bg-gradient" style={{ width: "20%" }}>Max Spread</Th>
              <Th className="bg-gradient" style={{ width: "20%" }}>Last Update</Th>
            </Tr>
          </Thead>
          <Tbody>
            {this.state.array.map((compare) =>
              <Compare key={compare.CoinId} compare={compare} />
            )}
          </Tbody>
        </Table>
      </div>
    );
  }

  async loadData() {
    let socket = new WebSocket(`wss://${General.getApi()}/SpreadHunter/SubscribeGroupInfo`);
    let i = 0;
    socket.addEventListener('message', (event) => {
      if (event.data instanceof Blob) {
        try {
          const reader = new FileReader();
          reader.onload = () => {
            const decompressedData = Pako.inflate(new Uint8Array(reader.result), { to: 'string' });
            const dataObject = JSON.parse(decompressedData);
            dataObject.sort((a,b)=> b.MaxSpread - a.MaxSpread);
            this.setState({array: dataObject});
            i++;
            if (i % 3 === 0) {
              socket.send("ping");
            }
          };
          reader.readAsArrayBuffer(event.data);
        }
        catch (error) {
          console.error(error);
        }
      }
    });
    socket.onclose = function (e) {
      console.log("WebSocket Error: ", e);
    };
  }
}
