import React, { Component } from 'react';
import { Networks } from "../components/Networks";

export class Symbol extends Component {

  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className="d-flex flex-column m-3 p-3 bg-gradient shadow">
        <div>{this.props.symbol.Name}</div>
        <a className='link-info text-reset' href={this.props.symbol.Link} target="_blank" rel="noopener noreferrer">{this.props.symbol.Exchange}</a>
        <div className='text-danger'>{this.props.symbol.BestAskPrice}</div>
        <div className='text-success'>{this.props.symbol.BestBidPrice}</div>
        <div className='text-white'>{new Date(this.props.symbol.LastUpdate).toLocaleString()}</div>
        {this.props.symbol.Networks?.length > 0 &&
          <Networks networks={this.props.symbol.Networks} symbol={this.props.symbol.Name} exchange={this.props.symbol.Exchange} price={this.props.symbol.BestAskPrice} />
        }
      </div>
    );
  }

}