import React, { Component } from 'react';
import { Networks } from "../components/Networks";

export class Group extends Component {

  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className='d-flex flex-column m-3 p-3 bg-gradient shadow'>
        <div className='d-flex flex-column justify-content-center'>
          <div className='d-flex justify-content-center'>
            {this.props.group.Name1}
            &nbsp;&nbsp;
            {this.props.group.Name2}
          </div>
          <div className='d-flex justify-content-center'>
            <a className='link-info text-reset' href={this.props.group.Link1} target="_blank" rel="noopener noreferrer">{this.props.group.Exchange1}</a>
            &nbsp;&nbsp;
            <a className='link-info text-reset' href={this.props.group.Link2} target="_blank" rel="noopener noreferrer">{this.props.group.Exchange2}</a>
          </div>

          <div className='text-white'>{new Date(this.props.group.LastUpdate1).toLocaleTimeString()}  {new Date(this.props.group.LastUpdate2).toLocaleTimeString()}</div>
          {this.props.group.Hedging ? (<a className='link-warning' href={this.props.group.Hedging} target="_blank" rel="noopener noreferrer">hedging</a>) : (<></>)}

          {this.props.group.Data.length > 0 &&
            <div className='d-grid text'>
              <div className='row'>
                <small className='col text-nowrap'>Spread %</small>
                <small className='col text-nowrap'>Volume $</small>
                <small className='col text-nowrap'>Profit $</small>
              </div>
              {this.props.group.Data.map((data, index) =>
                <div className='row' key={index}>
                  <small className='col'>{data.Spread}</small>
                  <small className='col'>{data.Volume}</small>
                  <small className='col'>{data.Profit}</small>
                </div>
              )}
            </div>
          }
          {this.props.group.Networks.length > 0 &&
            <Networks networks={this.props.group.Networks} symbol={this.props.group.Name1 + this.props.group.Name2} exchange={this.props.group.Exchange1} price={this.props.group.Data[0].AskPrice} />
          }

        </div>
      </div>
    );
  }

}