import React, { Component } from 'react';

export class Networks extends Component {

    constructor(props) {
        super(props);
        this.state = { networks: props.networks, symbol: props.symbol, exchange: props.exchange, price: props.price, index: -1 };
    }

    render() {
        return (
            <div>
                <div className="btn-group btn-group-sm my-2">
                    {this.state.networks.map((network, index) =>
                        <button key={index} className={this.state.index === index ? "btn btn-dark active": "btn btn-dark" } onClick={()=>{ this.setState({index: index}); }}>{network.Name}</button>
                    )}
                </div>
                <div>
                    {this.state.networks.map((network, index) =>
                        (this.state.index === index &&
                            (<div key={index} className="d-flex text-white justify-content-center">
                                <div className='d-flex flex-column'>
                                    <div>{`${network.RealName}`}</div>
                                    <div>Withdraw: {`${network.WithdrawEnabled}`}</div>
                                    <div>Deposit: {`${network.DepositEnabled}`}</div>
                                    <div>{"Withdraw fee: " + (network.WithdrawFee * this.state.price).toFixed(2) + " $"}</div>
                                    <div>{"Withdraw min: " + (network.WithdrawMin * this.state.price).toFixed(2) + " $"}</div>
                                    <div>{"Confirmations: " + network.Confirmations}</div>
                                </div>
                            </div>)
                        )
                    )}
                </div>
            </div>
        );
    }

}