import React, { Component } from 'react';
import { Symbol } from "./Symbol";
import { Tr, Td } from 'react-super-responsive-table';
import 'react-super-responsive-table/dist/SuperResponsiveTableStyle.css';
import { Group } from './Group';
import General from '../general';

function convertTime(lastUpdate) {
  const oldTicks = new Date(lastUpdate).getTime();
  const newTicks = new Date().getTime();
  const seconds = (newTicks - oldTicks) / 1000;
  if (seconds < 60) return { time:seconds.toFixed(0), symbol:'s' };
  else if (seconds < 3600) return { time:(seconds / 60).toFixed(0), symbol:'m' };
  else if (seconds < 86400) return { time:(seconds / 3600).toFixed(0), symbol:'h' };
  else return { time:(seconds / 86400).toFixed(0), symbol:'d' };
}

export class Compare extends Component {

  constructor(props) {
    super(props);
    this.update = this.update.bind(this);
  }

  async update() {
    let response = await fetch(`https://${General.getApi()}/SpreadHunter/Update?id=${this.props.compare.CoinId}`);
    if(!response.ok){
      let result = await response.text();
      alert(result);
    }
  }

  componentDidMount() {
    this.interval = setInterval(() => this.setState({ time: new Date().toUTCString() }), 1000);
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  render() {
    const find = this.props.compare.Groups.find(g=>g.Data !== undefined && g.Data.length > 0);
    let text = "text-danger";
    if(find !== undefined && find.Networks.length > 0) {
      text = "text-success";
    }
    const obj = convertTime(this.props.compare.LastUpdate);
    let color = 0;
    if(obj.symbol === "s") color = 255 - obj.time;
    else if(obj.symbol === "m") color = 255 - (obj.time * 60);

    if(color < 0) color = 0;
    
    return (
      <>
        <Tr data-bs-toggle="collapse" href={"#collapse" + this.props.compare.CoinId} role="button" aria-expanded="false" aria-controls="collapseExample">
          <Td className={text}><div title={this.props.compare.CoinId}>{this.props.compare.Asset}</div></Td>
          <Td>{this.props.compare.MaxSpread.toFixed(2)}</Td>
          <Td><span  style={{color:`rgba(255, ${color}, 5, 0.8)`}}>{obj.time} {obj.symbol}</span></Td>
        </Tr>
        <Tr className="collapse" id={"collapse" + this.props.compare.CoinId}>
          <Td colSpan="5">
            <div className='position-relative'>
              <button className=' position-absolute end-0 btn btn-outline-secondary btn-sm' onClick={this.update}>Update</button>
              <div className="d-flex flex-wrap justify-content-center">
                {this.props.compare.Groups.map((group) =>
                  <Group key={group.Exchange1 + group.Exchange2 + group.RealName1 + group.RealName2} group={group} />
                )}
              </div>
              <div className="d-flex flex-wrap justify-content-center">
                {this.props.compare.Symbols.map((symbol) =>
                  <Symbol key={symbol.Exchange + symbol.BaseAsset + symbol.QuoteAsset} symbol={symbol} />
                )}

              </div>
            </div>
          </Td>
        </Tr>
      </>
    );
  }

}
